import React from "react";
import cssVar from "views/mobileView/styles";
import SingleContentIconCard from "../molecules/Cards/SingleContentIconCards";
import { MdPictureInPictureAlt } from "react-icons/md"
import { Chip } from '@mui/material';
export default function SliderComponent({ navigation, type, title, slides, right }) {

    const renderRightSide = () => {
        switch (right) {
            case 'All funds':
                return (<span style={{
                    fontSize: cssVar.getPropertyValue('--font-helper'),
                    margin: "0px 20px 0px 0px",
                    padding: "2px 10px 2px 10px",
                    border: `1px solid ${cssVar.getPropertyValue('--primaryLite')}`,
                    borderRadius: "20px",
                    backgroundColor: cssVar.getPropertyValue('--primaryLite'),
                    color: cssVar.getPropertyValue('--primary')
                }}>
                    All Funds
                </span>);
            case 'All':
                return (<Chip>All Funds</Chip>);
            default:
                return <span style={{ padding: 5 }}></span>;
        }
    };
    const hci_card = (slide) => {
        return (<div
            key={slide.key}
            style={{
                margin: "1% 2% 1% 4%",
                padding: "6% 0 0 4%",
                border: "1px solid " + cssVar.getPropertyValue("--divider"),
                borderRadius: 10,
                backgroundColor: slide.backgroundColor,
                display: "inline-block",
                width: "80%",
                minheight: "120px",
                whiteSpace: "normal",
                flex: 1
            }}
        >
            <div style={{ color: slide.titleColor, fontSize: cssVar.getPropertyValue('--font-normal'), fontWeight: "500", marginBottom: "4%" }}>
                {slide.title}
            </div>
            <div style={{ display: "flex" }}>
                <div style={{ flex: "7" }}>
                    <p style={{ fontSize: cssVar.getPropertyValue('--font-helper'), fontWeight: "400", color: cssVar.getPropertyValue("--textLight"), marginBottom: 0 }}>
                        {slide.text}
                    </p>
                </div>
                <div
                    style={{
                        flex: "3"
                    }}
                >
                    {slide.icon}
                </div>
            </div>
        </div>)
    }

    const video_card = (slide) => {
        return (
            <a href={`https://www.youtube.com/embed/${slide.id}/`} target="_BLANK">
            <div
                key={slide.key}
                style={{
                    margin: "1% 3% 4% 4%",
                    borderRadius: 10,
                    display: "inline-block",
                    width: "80%",
                    whiteSpace: "normal"
                }}
                onClick={() => {
                    
                    slide.onClick()
                }}>
                <div style={{ position: "relative" }}>
                    <img
                        src={`https://img.youtube.com/vi/${slide.id}/maxresdefault.jpg`}
                        style={{
                            paddingVertical: 10,
                            width: "100%",
                            borderRadius: 10,
                            filter: slide.id === slide.currentVideoID ? 'blur(5px)' : 0,
                        }} />
                    <img
                        src='https://cdn4.iconfinder.com/data/icons/logos-and-brands/512/395_Youtube_logo-512.png'
                        style={{
                            position: 'absolute',
                            width: "40px",
                            bottom: 8,
                            right: 16,
                            filter: slide.id === slide.currentVideoID ? 'blur(1px)' : 0,
                        }} />
                    {slide.id === slide.currentVideoID ?
                        <div style={{ width: "100%", textAlign: "center", position: "absolute", top: "40%" }}>
                            <MdPictureInPictureAlt style={{ paddingHorizontal: 10, color: cssVar.getPropertyValue("--primary") }} size={24} />
                            <p style={{ color: "#fff", }}>This video is playing in picture-in-picture</p>
                        </div>
                        : null}
                </div>
                </div>
            </a>
        )
    }
    const blog_card = (slide) => {
        return (
            <div
                key={slide.key}
                onClick={() => slide.onClick()}
                style={{
                    border: "1px solid #9eadbd4D",
                    backgroundColor: slide.backgroundColor || "#fff",
                    padding: 10,
                    width: "60%",
                    alignItems: "stretch",
                    margin: "4% 4% 50% 4%",
                    borderRadius: 10,
                    display: "inline-block",
                    whiteSpace: "normal"
                }}>
                <img
                    src='https://blog.feedspot.com/wp-content/uploads/2016/01/Personal-Finance-Blogs.jpg'
                    style={{
                        paddingVertical: 10,
                        height: "12%",
                        width: "100%",
                        borderRadius: 5
                    }} />
                <p style={{ color: cssVar.getPropertyValue("--textLight"), fontSize: cssVar.getPropertyValue('--font-normal'), lineHeight: "18px", paddingTop: 5 }}>{slide.title}</p>
            </div>
        );
    }
    return (
        <div>
            <div style={{ marginLeft: "16px", display: 'flex', flex: 1, flexDirection: 'row', justifyContent: 'space-between' }}>
                <span style={{ fontSize: cssVar.getPropertyValue('--font-head'), margin: "15px 0px 10px 0px" }}>{title}</span>
                <span style={{ margin: "15px 0px 10px 0px" }}>
                    {renderRightSide()}
                </span>
            </div>
            <div style={{ overflowX: "auto", whiteSpace: "nowrap" }}>
                {type === "hci" && slides.map(slide => (
                    hci_card(slide)
                ))}
                {type === "videoView" && slides.map(slide => (
                    video_card(slide)
                ))}
                {type === "blogView" && slides.map(slide => (
                    blog_card(slide)
                ))}
            </div>
        </div>
    )
};

