import React from 'react';
import { Button, Spinner } from 'react-bootstrap';
import { AiOutlineRight } from 'react-icons/ai';
import { toast } from 'react-toastify';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import AdminServices from '../../../../../services/api/AdminServices';
import AuthHeader from '../../../components/molecules/Headers/AuthHeader';

export default function NameScreen(props) {

  const [name, setName] = React.useState('');
  const [loading, setloading] = React.useState(true);
	const [nextbtnPressed, setnextbtnPressed] = React.useState(false)
  const { state } = props.location;
  // const state = {mobile:"9768928535",verify:"MOBILE"}
  // const state = {email:"obhogle@gmail.com",verify:"EMAIL"}


  React.useEffect(() => {
    if (state === null || state === undefined) {
      props.history.push('/signup')
    }
    else {
      setloading(false)
    }
  }, [])

  const validateInput = () => {
      return name.length > 1 && !nextbtnPressed
  }

  const onNextBtnPressed = async () => {
		setnextbtnPressed(true);
    props.history.push({
      pathname: '/verify_otp1',
      state: {
        name,
        ...props.location.state
      }
    })
  };


  if (loading) {
    return (
      <div className="center-screen">
        <Spinner
          as="span"
          animation="grow"
          size="sm"
          role="status"
          aria-hidden="true"
        />
      </div>
    );
  } else {
    return <div className="container">
      <AuthHeader navigation={props.history} left='login'/>
      <h3 className="textHeadauth">Hello there!</h3>
      <p className="textNormal">Please enter your first name</p>
      <div className="floating-form">
        <FloatingTextInput
          label="First Name"
          autoFocus
          value={name}
          onChange={(e) => { 
            const a = e.target.value.replace(/[^a-zA-Z\s]/g, '');
            setName(a) }}
        />
      </div>
      <div className="footer">
        <Button style={{ opacity: validateInput() ? 1 : 0.4 }}
          onClick={() => { validateInput() && onNextBtnPressed() }}
          // onClick={()=>props.history.push({
          //   pathname: '/verify_otp1'
          // })}
          variant="primary" className="btn-footer">
          Next <AiOutlineRight size={14}/>
        </Button>
      </div>
    </div>
  }
}
