import React from 'react';
// import { CurrencyFormat } from '../../atoms/CurrencyFormat';
import cssVar from "views/mobileView/styles";
import { CurrencyFormat } from '../../atoms/CurrencyFormat';

const HomePortfolioCard = ({ header, navigation, data }) => {

  return (
    <div onPress={() => { navigation.push('/Portfolio'); }} style={{ position: 'relative' }}>
      <div style={{ flex: 1, borderRadius: 5, height: 'auto', margin:"4%" }}>
        <div className="mt-2" style={{ fontSize: cssVar.getPropertyValue("--font-normal") }}>
          {header}
        </div>

        <div style={{ fontSize: cssVar.getPropertyValue("--font-numHead") }}>
          {<CurrencyFormat num={data[0]} />} <span style={{ color: cssVar.getPropertyValue("--primary"), fontSize: "16px"}}>&#x2192;</span>
        </div>
        {!data[1].includes('-') ? (
          <div style={{ color: cssVar.getPropertyValue("--primary") }}>
            &#x25B2; {<CurrencyFormat num={data[2]} />} ({`${Number(data[1]).toFixed(2)}%`})
          </div>
        ) : (
          <div style={{ color: 'red' }}>
            &#x25BC; {data[2]} ({`${Number(data[1]).toFixed(2)}%`})
          </div>
        )}
      </div>
    </div>
  );
};

export default HomePortfolioCard;
